<template>
  <div class=''>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb> 
    <!--  --> 
    <el-card>
      <!--  -->
      <!-- <search-input :parentData="queryInfo.query" :parentURL="propsURL"></search-input> -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getOrdersList">
            <el-button slot="append" icon="el-icon-search" @click="getOrdersList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!-- table -->
      <el-table :data="ordersList" stripe border style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="order_number" label="" width="220"></el-table-column>
        <el-table-column prop="order_price" label=""></el-table-column>
        <el-table-column prop="pay_status" label="">
          <template v-slot="scope">
            <el-tag v-if="scope.row.pay_status === '1'" type="success" size="mini"></el-tag>
            <el-tag v-else type="warning" size="mini"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""></el-table-column>
        <el-table-column prop="create_time" label="">
          <template v-slot="scope">
            {{scope.row.create_time | timerFormat}}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template v-slot="scope">
            <el-tooltip :enterable="false" class="item" effect="dark" content="" placement="top">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="editDialog(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip :enterable="false" class="item" effect="dark" content="" placement="top">
              <el-button type="success" icon="el-icon-location" size="mini" @click="locationDialog(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <pagination :queryInfo="queryInfo" :total="total" :get-data="getOrdersList"></pagination>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editAddrDialogVisible"
        width="50%"
        @close="resetEditAddrDialog">
        <el-form :model="editForm" :rules="editFormRrules" ref="editFormRef" label-width="100px">
          <el-form-item label="/" prop="addr1">
            <!--  -->
            <el-cascader
              ref="cascaderRef"
              v-model="selectedKeys"
              :options="cityData"
              :props="propsObj"
              @change="handleChange">
            </el-cascader>
          </el-form-item>
          <el-form-item label="" prop="addr2">
            <el-input v-model="editForm.addr2"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editAddrDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editAddrDialogVisible = false"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="logisticsDialogVisible"
        width="50%">
        <el-timeline :reverse="true">
          <el-timeline-item
            v-for="(activity, index) in logistics"
            :key="index"
            :timestamp="activity.time">
            {{ activity.context }}
          </el-timeline-item>
        </el-timeline>
        <span slot="footer" class="dialog-footer">
          <el-button @click="logisticsDialogVisible = false"> </el-button>
          <el-button type="primary" @click="logisticsDialogVisible = false"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import Pagination from '../other/Pagination.vue'
import request from '../../network/request'
import cityData from './citydata'
import logistics from './logistics'
export default {
  name: 'Orders',
  components: {
    Pagination,
  },
  data () {
    return {
      //
      radio: 3,
      //
      ordersList: [],
      //
      queryInfo: {
        query: '',
        pagesize: 5,
        pagenum: 1
      },
      //
      total: 0,
      //
      propsURL: {
        url: 'orders',
        params: this.queryInfo
      },
      //
      editForm: {
        addr1: '',
        addr2: ''
      },
      //
      editFormRrules: {
        addr1: [
          { required: true, message: '', trigger: 'blur' },
        ],
        addr2: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },
      dialogVisible: '',
      //
      cityData,
      //
      propsObj: {
        expandTrigger: 'hover',
        checkStrictly: true,
        value: 'value',
        label: 'label',
        children: 'children',
      },
      //
      selectedKeys: [],
      //
      // locationDialogVisible: false,
      //(,)
      logistics,
      //
      editAddrDialogVisible: false,
      //
      logisticsDialogVisible: false,

    };
  },
  created() {
    this.getOrdersList()
  },
  methods: {
    //
    async getOrdersList() {
      const {data: res} = await request({
        url: 'orders',
        params: this.queryInfo
      })
      // console.log(res);
      if(res.meta.status !== 200) {
        return this.$message.error('!')
      }
      console.log('!');
      this.ordersList = res.data.goods;
      this.total = res.data.total;
    },
    //
    editDialog() {

      this.editAddrDialogVisible = true;
    },
    //
    resetEditAddrDialog() {
      this.$refs.editFormRef.resetFields()
      this.selectedKeys = []
    },
    //
    locationDialog(row) {
      //(,)
      // const {data: res} = await request({
      //   url: `/kuaidi/row.order_id`
      // })
      // console.log(res);

      this.logisticsDialogVisible = true;
    },
    //
    handleChange() {
      //
      this.$refs.cascaderRef.dropDownVisible = false;
    }
  },
}
</script>

<style lang='less' scoped>
  .el-cascader {
    width: 100%;
  }
</style>